import React from 'react'
import { Card, CardContent, Typography, Grid } from '@material-ui/core'
import styles from './Cards.module.css'
import CountUp from 'react-countup'
import cx from 'classnames'

const Cards = ({
  data: { confirmed, recovered, deaths, lastUpdate },
  country
}) => {
  if (!confirmed) {
    return 'Carregando as informa√ß√µes...'
  }
  const active = confirmed.value - recovered.value - deaths.value
  const cardDetails = [
    {
      style: styles.infected,
      text: 'Infectados',
      value: confirmed.value,
      bottomText: 'N√∫mero de infectados de COVID-19'
    },
    {
      style: styles.recovered,
      text: 'Recuperados',
      value: recovered.value,
      bottomText: 'N√∫mero de recuperados do COVID-19'
    },
    {
      style: styles.deaths,
      text: '√ìbitos',
      value: deaths.value,
      bottomText: 'N√∫mero de √≥bitos por COVID-19'
    },
    {
      style: styles.active,
      text: 'Casos Ativos',
      value: active,
      bottomText: 'N√∫mero de casos ativos de COVID-19'
    }
  ]
  return (
    <div className={styles.container}>
      <Grid container spacing={3} justify='center'>
        {cardDetails.map((detail, index) => (
          <Grid
            item
            component={Card}
            xs={12}
            md={2}
            className={cx(styles.Card, detail.style)}
            key={index}
            style={{ margin: '0px 23.675px', padding: '12px' }}
          >
            <CardContent>
              <center>
                <Typography color='textPrimary' gutterBottom>
                  <b>{detail.text}</b>
                </Typography>
              </center>
              <center>
                <Typography variant='h5'>
                  <CountUp
                    start={0}
                    end={detail.value}
                    duration={2}
                    separator=','
                  />
                </Typography>
              </center>
              <br />
              <Typography color='textPrimary'>Ultima atualiza√ß√£o: </Typography>
              <Typography color='textSecondary' variant='body2'>
                {new Date(lastUpdate).toDateString()}
              </Typography>
              <Typography color='textSecondary' variant='body2'>
                {new Date(lastUpdate).toLocaleTimeString()}
              </Typography>
              <br />
              <Typography variant='body2'>{detail.bottomText}</Typography>
              <hr data-symbol='‚àû' />
              <center id='card-country-name'><Typography color='textPrimary'>{country}</Typography></center>
            </CardContent>
          </Grid>
        ))}
      </Grid>
    </div>
  )
}

export default Cards
